.collapsing-animation {
  @include collapsing-animation;
}

.invisible--animated {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s linear 500ms, opacity 500ms;
}

.visible--animated {
  visibility: visible;
  opacity: 1;
  transition: visibility 0s, opacity 500ms;
}



