.date-selection {
  @include table;
  @include fullwidth;

  & > .date-selection--select {
    @include table-cell;
    width: 100% * 1 / 3;
    padding-left: 2%;

    &:first-child {
      @include pl0;
    }

    .icon {
      @include pblue;
    }
  }
}



